.vidPlayer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sub {
    display: flex;
    margin: 10px;
}

.likes {
    margin-left: 10px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    background-color: rgb(117, 117, 117);
    border-radius: 10px;
    display: flex;
    font-size: 20px;
}

.like {
    padding-left: 10px;
    padding-right: 10px;
}

.commentBtn {
    background-color: grey;
    color: white;
    right: 20px;
    border: none;
    border-radius: 10px;
}